import React, { useState } from "react"
import { Nav } from "grommet"
import SidebarButton from "./SidebarButton"
import Setting from "./../Settings"
import useLocalStorage from "./../../hooks/useLocalStorage"
import { ROUTES } from "./../../routes/routes"

const SidebarFooter = ({ expanded, onModeChange }) => {
  const [showSettings, setShowSettings] = useState(false)
  const [darkMode, setDarkMode] = useLocalStorage("isDarkMode", false)

  const handleSettingsSave = isDarkMode => {
    setDarkMode(isDarkMode)
    setShowSettings(false)
    onModeChange(isDarkMode)
  }
  const handleSettingsClose = () => {
    setShowSettings(false)
  }

  const handleSettingsOpen = () => {
    setShowSettings(true)
  }

  return (
    <Nav>
      <SidebarButton onClick={handleSettingsOpen} icon={ROUTES.SETTINGS} label={expanded ? "Language" : ""} />
      {showSettings && (
        <Setting
          open={showSettings}
          darkMode={darkMode}
          onClose={handleSettingsClose}
          onSave={handleSettingsSave}
        ></Setting>
      )}
    </Nav>
  )
}
export default SidebarFooter
