import { useLocation, useHistory } from "react-router-dom"

const useNavigate = () => {
  const location = useLocation()
  const history = useHistory()

  const navigate = path => {
    if (path != location.pathname) {
      history.push(path)
    }
  }

  return {
    navigate: navigate,
    location: location,
    history: history,
  }
}

export default useNavigate
