import React from "react"
import { Switch, Route, Redirect } from "react-router-dom"
import { Main, Box } from "grommet"
import loadable from "@loadable/component"
import { ROUTES } from "./routes"

const Router = () => {
  return (
    <Main>
      <Box>
        <Switch>
          <Route path={ROUTES.AGENTS} exact={true}>
            <AGENTS />
          </Route>
          <Route path={ROUTES.DETAILS} exact={true}>
            <Redirect to={`${ROUTES.DETAILS}/s`} />
          </Route>
          <Route path={ROUTES.SUPPORT} exact={true}>
            <Support />
          </Route>
          <Route path={`${ROUTES.DETAILS}/:id`}>
            <Details />
          </Route>
          <Route>
            <Redirect to={ROUTES.AGENTS} />
          </Route>
        </Switch>
      </Box>
    </Main>
  )
}

const AGENTS = loadable(() => import("../pages/agents"))
const Support = loadable(() => import("../pages/support"))
const Details = loadable(() => import("../pages/details"))

export default Router
