import React from "react"
import { Box, Sidebar } from "grommet"
import SidebarHeader from "./SidebarHeader"
import SidebarFooter from "./SidebarFotter"
import MainNavigation from "./MainNavigation"

const ExpandableSidebar = ({ expanded, onModeChange }) => (
  <Box
    background="light-1"
    direction="row"
    height={{ min: "100%" }}
    pad="small"
    style={{ padding: 0, borderRight: "1px solid #DDD" }}
  >
    <Sidebar responsive={true} footer={<SidebarFooter expanded={expanded} onModeChange={onModeChange} />}>
      <MainNavigation expanded={expanded} />
    </Sidebar>
  </Box>
)

export default ExpandableSidebar
