import React, { useState } from "react"
import { Box, Button, Layer, CheckBox, Heading } from "grommet"

const Setting = ({ open, onClose, darkMode, onSave }) => {
  const [isDarkMode, setDarkMode] = useState(darkMode)
  const onChange = event => setDarkMode(event.target.checked)

  return (
    open && (
      <Layer onEsc={onClose} onClickOutside={onClose}>
        <Box width="large" height="small" pad={"small"} justify="end">
          <Heading level={3}>Language</Heading>
          <CheckBox toggle label="Arabic" checked={isDarkMode} onChange={onChange} />
          <Box
            flex
            direction="row"
            justify="end"
            align="end"
            height="700px"
            width="300px"
            gap="medium"
            margin={{ top: "small", left: "auto" }}
          >
            <Button fill="horizontal" label="Cancel" onClick={onClose} />
            <Button fill="horizontal" label="Save" onClick={() => onSave(isDarkMode)} />
          </Box>
        </Box>
      </Layer>
    )
  )
}

export default Setting
