import React from "react"
import pt from "prop-types"
import { Box, Text } from "grommet"
import useHover from "./../../hooks/userHover"
import { BarChart, Help, Group, Home } from "grommet-icons"

import { ROUTES } from "./../../routes/routes"
const SidebarButton = ({ active, icon, label, onClick }) => {
  const [hoverRef, isHovered] = useHover()

  return (
    <Box
      pad={{ vertical: "small", horizontal: "medium" }}
      hoverIndicator={{ background: "light-1" }}
      ref={hoverRef}
      background={active || isHovered ? "light-3" : "transparent"}
      style={{
        opacity: isHovered ? "0.9" : "1",
      }}
      onClick={onClick}
      direction="row"
      flex
    >
      {icon && getIcon(icon, isHovered, active)}
      <Text color={active || isHovered ? "brand" : "brand"}>{label}</Text>
    </Box>
  )
}

export default SidebarButton

SidebarButton.propTypes = {
  active: pt.bool,
  icon: pt.string,
  label: pt.string,
  onClick: pt.func,
}

export { ICONS }

const getIcon = (icon, isHover, active) => {
  const Icon = ICONS[icon]
  const d = <Icon style={{ marginRight: 10 }} color={isHover || active ? "brand" : "brand"} />

  return d
}

const ICONS = {
  [ROUTES.AGENTS]: Home,
  [ROUTES.DETAILS]: Group,
  [ROUTES.SUPPORT]: Help,
}
