import React from "react"
import pt from "prop-types"
import { Nav } from "grommet"
import SidebarButton from "./SidebarButton"
import { ROUTES } from "./../../routes/routes"
import useNavigate from "../../hooks/useNavigate"

const MainNavigation = ({ expanded }) => {
  const isActive = path => path && path === location.pathname
  const { navigate, location } = useNavigate()

  return (
    <Nav responsive={true}>
      <SidebarButton
        link={ROUTES.AGENTS}
        active={isActive(ROUTES.AGENTS)}
        icon={ROUTES.AGENTS}
        onClick={() => navigate(ROUTES.AGENTS)}
        label={expanded ? "Home" : ""}
      />
      <SidebarButton
        link={ROUTES.DETAILS}
        active={location.pathname.includes(ROUTES.DETAILS)}
        onClick={() => navigate(ROUTES.DETAILS)}
        icon={ROUTES.DETAILS}
        label={expanded ? "Find Realtor" : ""}
      />
    </Nav>
  )
}

MainNavigation.propTypes = {
  expanded: pt.bool,
}

export default MainNavigation
